.ui-change-btn {
    width: fit-content;
    padding: 1rem 2rem;
    display: block;
    font-size: 1rem;
    background-color: var(--color-white);
    border: 2px solid var(--color-primary);
    border-radius: 0.5rem;
    cursor: pointer;
  }
  .ui-change-btn span {
    background-color: var(--color-danger);
    display: inline-block;
    padding: 0.4rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
    color: var(--color-white);
  }